<template>
  <!-- 根容器 -->
  <div class="root-container">
    <!-- 主容器 -->
    <div class="container">
      <!-- 顶部部分，显示知识候选项 -->
      <div class="top-section">
        <!-- 知识候选项容器 -->
        <div class="knowledge-candidates" style="display: flex; width: 100%">
          <!-- 循环渲染知识候选项 -->
          <div
            v-for="candidate in knowledgeCandidates"
            :key="candidate.id"
            class="candidate-item"
            style="flex: 1"
          >
            <!-- 候选项信息 -->
            <div class="candidate-info">
              <!-- 候选项标题 -->
              <div style="margin-right: 20px">{{ candidate.title }}</div>
              <!-- 候选项内容 -->
              <div>
                <p v-for="(candidate, idx) in candidate.content.split(',')" :key="idx">
                  <!-- 复选框 -->
                  <label for=""> <input type="checkbox" :name="idx" /> <span style="font-size: 22px;">{{candidate}}</span> </label>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      <!-- 底部部分，显示超关系图 -->
      <div class="bottom-section">
        <!-- 超关系图容器 -->
        <div class="relation-slider">
          <!-- 超关系容器 -->
          <div class="relation-container">
            <!-- 循环渲染超关系 -->
            <div class="relation-item-row" style="height: 50vh;min-height:500px width: 100%">
              <!-- 超关系图1 -->
              <div class="relation-item" style="height: 100%;width: 100%">
                <jsMind refid="mind1" v-if="Library" :mind="mind1"></jsMind>
              </div>
              <!-- 超关系图2 -->
              <div class="relation-item" style="height: 100%; width: 100%">
                <jsMind refid="mind2" v-if="Library" :mind="mind2"></jsMind>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 按钮组 -->
      <div class="button-group">
        <!-- 确认知识库按钮 -->
        <button class="confirm-library-btn" @click="confirmLibrary">确认知识库</button>
        <!-- 超关系确认按钮 -->
        <button class="confirm-relation-btn" @click="confirmRelation">超关系确认</button>
      </div>
    </div>
    <!-- 提示框组件 -->
    <Tip ref="tip"></Tip>
  </div>
</template>

<script>
import jsMind from "./components/JsMind";
import Tip from "./components/Tip";

export default {
  data() {
    return {
      // 知识候选项数据
      knowledgeCandidates: [
        { id: 1, title: "entity:", content: "CDE,药品化学名,治疗,高血压", waiting: false },
        { id: 2, title: "relation:", content: "CDE,药品化学名,治疗,高血压", waiting: false },
        { id: 3, title: "key:", content: "CDE,药品化学名,治疗,高血压", waiting: false },
        { id: 4, title: "value:", content: "CDE,药品化学名,治疗,高血压", waiting: false },
      ],

      // 确认知识库标志
      Library: false,
      // 确认超关系标志
      Relation: false,
      
      // 超关系图1数据
      mind1: {
        // 元数据
        meta: {
          name: "治疗",
        },
        format: "node_tree",
        // 根节点
        data: {
          id: "root",
          topic: "治疗",
          children: [
            // 子节点
            {
              id: "CDE",
              topic: "CDE",
              direction: "right",
              children: [],
            },
            // ...
          ],
        },
      },
      
      // 超关系图2数据
      mind2: {
        // 元数据
        meta: {
          name: "治疗",
        },
        format: "node_tree",
        // 根节点
        data: {
          id: "root1",
          topic: "治疗",
          children: [
            // 子节点
            {
              id: "应用他汀类药物1",
              topic: "应用他汀类药物",
              direction: "right",
              children: [],
            },
            // ...
          ],
        },
      },
    };
  },
  components: {
    // 注册子组件
    jsMind,
    Tip,
  },
  mounted() {},

  methods: {
    // 确认知识库
    confirmLibrary() {
      this.Library = true;
    },
    // 确认超关系
    confirmRelation() {
      // 打开提示框
      this.$refs.tip.open();
    },
  },
};
</script>

<style>
