}

/* 全局样式重置（已注释掉，不起作用） */
/* * {
  padding: 0;
  margin: 0;
} */

/* 主容器样式 */
.container {
  display: flex;
  flex-direction: column;
  height: 100vh; /* 设置容器占满整个视窗高度 */
