<template>
  <!-- 显示弹窗容器，同时绑定点击事件，用于点击空白区域时触发取消事件 -->
  <div class="app" v-if="isShow" @click="blank">
    <div class="card">
      <div class="text">
        <!-- 数据内容 -->
        "data": [ ... ]
      </div>
      <br />
      <div class="btn">
        <!-- 取消按钮 -->
        <button @click="cancel">取消</button>
        <!-- 确认按钮 -->
        <button @click="confirm" class="confirm">确认</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "SpringFrame",
  props: {
    title: {
      type: String,
      default: "标题",
    },
  },
  data() {
    return {
      isShow: false, // 控制弹窗是否显示
    };
  },
  computed: {},
  methods: {
    // 点击取消按钮时触发的方法，同时通过 $emit 触发 "cancel" 事件
    cancel() {
      this.isShow = false;
      this.$emit("cancel"); // 触发 "cancel" 事件
    },
    // 打开弹窗
    open() {
      this.isShow = true;
    },
    // 点击确认按钮时触发的方法，同时通过 $emit 触发 "confirm" 事件
    confirm() {
      this.isShow = false;
      this.$emit("confirm"); // 触发 "confirm" 事件
    },
    // 点击空白区域时触发的方法，相当于点击取消按钮
    blank(e) {
      /* 判断点击的节点是否为最外层容器 */
      if (e.target.className == "app") {
        this.cancel();
      }
    },
  },
  watch: {},
  created() {},
  mounted() {},
};
</script>
<style scoped>
.app {
  position: fixed;
  inset: 0;
  z-index: 10000;
  background-color: rgba(194, 194, 194, 0.2);
}
.card {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 250px;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 8px rgb(219, 219, 219);
  background-color: #fff;
  padding: 10px 15px;
}
.card > h2 {
  text-align: center;
  font-size: 20px;
  line-height: 25px;
}
.text {
  min-height: 100px;
  max-height: 70vh;
  max-width: 600px;
  padding: 20px;
  overflow: auto;
}
.btn > button {
  padding: 5px;
  margin-left: 5px;
  float: right;
  text-align: center;
  font-family: "fangsong";
  font-weight: bold;
  font-size: 18px;
  border: none;
  box-shadow: -2px -2px 8px -2px white, 2px 2px 8px -2px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  cursor: pointer;
}
button:active {
  box-shadow: inset -2px -2px 8px -2px white, inset 2px 2px 8px -2px rgba(0, 0, 0, 0.15);
}
.confirm {
  background-color: #53b5df;
  color: white;
  border: 0px solid;
  padding: 5px 10px;
  border-radius: 10px;
  font-size: 15px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  margin: 0 10px;
  /* 添加按钮之间的水平间距 */
  white-space: nowrap;
  color: #fff;
}
</style>
